@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap);
.header {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	padding: 1rem 2rem;
}

.header-components {
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
}

.header-user,
.header-user_coins {
	display: flex;
	align-items: center;
}

.header-user {
	justify-content: flex-end;
}

.header-user span {
	font-size: 1.2rem;
	color: #616161;
	margin-right: 1rem;
}

.header-user_coins {
	position: relative;
	background-color: #ededed;
	border-radius: 100px;
	padding: 0.3rem 0.7rem;
}

.header-user_coins span {
	margin-right: 0.5rem;
}

.header-user_coins img {
	cursor: pointer;
}

.header-menu_coins {
	position: absolute;
	top: 120%;
	left: 0;
	z-index: 50;
	background-color: #ededed;
	list-style: none;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 0.5rem;
	border-radius: 5px;
	-webkit-animation: scale-up-ver-top 0.4s cubic-bezier(0.39, 0.575, 0.565, 1)
		both;
	animation: scale-up-ver-top 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

/* Generated by Animista
 Licensed under FreeBSD License */

@-webkit-keyframes scale-up-ver-top {
	0% {
		-webkit-transform: scaleY(0.4);
		transform: scaleY(0.4);
		-webkit-transform-origin: 100% 0%;
		transform-origin: 100% 0%;
	}
	100% {
		-webkit-transform: scaleY(1);
		transform: scaleY(1);
		-webkit-transform-origin: 100% 0%;
		transform-origin: 100% 0%;
	}
}
@keyframes scale-up-ver-top {
	0% {
		-webkit-transform: scaleY(0.4);
		transform: scaleY(0.4);
		-webkit-transform-origin: 100% 0%;
		transform-origin: 100% 0%;
	}
	100% {
		-webkit-transform: scaleY(1);
		transform: scaleY(1);
		-webkit-transform-origin: 100% 0%;
		transform-origin: 100% 0%;
	}
}

.header-menu_coins h4 {
	margin-bottom: 0.5rem;
}

.header-menu_coins li {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.header-user_redeemed {
	cursor: pointer;
	margin-left: 1rem;
	padding: 0.5rem;
	font-size: 1rem;
	border: none;
	background-color: #0ad4fa;
	color: #ffffff;
	border-radius: 5px;
}

.header-user_redeemed:hover {
	background-color: #08bbdf;
}

/* Media queries */
@media (max-width: 37.5rem) {
	/* 600px */
	.header {
		flex-direction: column;
		padding: 1rem 1rem;
		align-items: stretch;
	}
	.header-components {
		justify-content: flex-end;
	}
	.header-user {
		width: 100%;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
	}

	.header-user span {
		font-size: 1rem;
	}

	.header-user_redeemed {
		margin: 1rem 0 0 0;
		text-decoration: none;
		width: 100%;
	}
}

.productRedeem {
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 9px 10px 10px -5px rgba(0, 0, 0, 0.33);
	-webkit-box-shadow: 9px 10px 10px -5px rgba(0, 0, 0, 0.33);
	-moz-box-shadow: 9px 10px 10px -5px rgba(0, 0, 0, 0.33);
	border-radius: 5px;
}
.productRedeem-img {
	max-width: 50%;
	object-fit: cover;
}

.productRedeem-info {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0.5rem;
}

.productRedeem-info_cost {
	display: flex;
	justify-content: center;
	margin: 0;
	margin-top: 0.3rem;
}

.productRedeem-info_cost b {
	padding: 0 0.3rem;
}

.productRedeem-info_cost img {
	max-width: 25%;
	padding: 0 0.3rem;
}

.redeemHistory {
	text-align: center;
}

.redeemHistory h2,
.redeemHistory p {
	margin: 1rem 0;
}

.redeemHistory h2 {
	color: #08bbdf;
}

.redeemHistory-products {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 0.8rem;
	gap: 0.8rem;
	padding: 0 5rem;
	margin-bottom: 3rem;
}

/* Media queries */
@media (max-width: 40rem) {
	/* 640px */
	.redeemHistory-products {
		padding: 0 2rem;
		grid-template-columns: repeat(1, 1fr);
	}
}

@media (min-width: 40rem) and (max-width: 52.5rem) {
	/* 640px - 720px */
	.redeemHistory-products {
		padding: 0 2rem;
		grid-template-columns: repeat(2, 1fr);
	}
}

.paginationBar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 2rem 8rem 0 8rem;
	font-size: 1.2rem;
}

.paginationBar-sort {
	display: flex;
	align-items: center;
	width: 15%;
	justify-content: space-around;
}

.paginationBar-sort > span:first-child {
	color: #616161;
}

.paginationBar-sort > span:nth-child(3) {
	color: #a3a3a3;
}

.paginationBar-sort_verticalLine {
	border-left: 2px solid #d9d9d9;
	height: 3rem;
}

.paginationBar-separator {
	border-bottom: 2px solid #d9d9d9;
	margin: 1rem 8rem;
}

.paginationBar-arrows {
	display: flex;
	width: 12%;
	justify-content: space-around;
	align-items: center;
}

/* Arrow styles */
.prev,
.next {
	border: none;
	border-radius: 50%;
	box-shadow: 0px -1px 18px 1px rgba(8, 187, 223, 1);
	cursor: pointer;
}

.prev.disabled,
.next.disabled {
	pointer-events: none;
	box-shadow: none;
	color: #999;
}

/* Media queries */

@media (max-width: 62rem) {
	/* 992px */

	.paginationBar {
		width: 100%;
		padding: 1rem 2rem;
		font-size: 1rem;
		justify-content: center;
	}

	.paginationBar-sort {
		justify-content: space-evenly;
		width: 50%;
	}

	.paginationBar-arrows {
		width: 35%;
	}

	.paginationBar-separator {
		margin: 0 2rem 1rem 2rem;
	}
}

@media (min-width: 45rem) {
	/* 720px */
	.paginationBar {
		font-size: 1.2rem;
	}
}

.filterBar {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	width: 35%;
	top: 40%;
	left: 22%;
	z-index: 100;
}

.filterBar span {
	color: #9c9c9c;
	font-size: 1rem;
}

.filterBar-sort_filter {
	background-color: #0ad4fa;
	color: #ffffff;
	border: none;
	border-radius: 100px;
	padding: 0.5rem 1rem;
	cursor: pointer;
	font-size: 1rem;
}

/* Media queries */
@media (max-width: 62rem) {
	/* 992px */
	.filterBar {
		padding: 0 1rem;
		position: relative;
		width: 100%;
		top: 0;
		left: 0;
		flex-wrap: wrap;
	}
	.filterBar-sort_filter {
		margin-top: 0.5rem;
	}
}

@media (min-width: 62rem) and (max-width: 75rem) {
	/* 992-1200px */
	.filterBar {
		left: 25%;
		width: 40%;
	}
}

@media (min-width: 45rem) and (max-width: 62rem) {
	/* 720px-992px */
	.filterBar {
		padding: 0 6rem;
	}
}

.product {
	box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
	width: 16rem;
	height: 16rem;
	display: flex;
	flex-direction: column;
	position: relative;
	padding-bottom: 3rem;
	transition: all 0.3s ease-in;
	-webkit-transform: translate(0);
	        transform: translate(0);
	border-radius: 5px;
	background-color: #ffffff;
}

.product-buyIcon {
	position: absolute;
	top: 5%;
	left: 85%;
	width: 11%;
}

.product-img {
	max-width: 100%;
	object-fit: contain;
	border-radius: 5px;
}

.product-separator {
	border-bottom: 2px solid #d9d9d9;
	margin: 0rem 1rem 1rem 1rem;
}

.product span,
.product h3 {
	padding-left: 1rem;
}

.product span {
	font-size: 1rem;
	color: #a3a3a3;
}

.product h3 {
	font-size: 1.12rem;
	color: #616161;
	font-weight: 600;
}

/* Redeem styles */
.RedeemInfoContainer {
	position: absolute;
	top: 0;
	left: 50%;
	padding: 0.3rem 0.5rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
	border-radius: 50px;
	background-color: #616161;
	opacity: 0.8;
}

.RedeemInfoContainer p {
	font-size: 0.8em;
	color: #fff;
	padding-right: 0.2rem;
}

.RedeemInfoContainer img {
	padding: 0.18rem;
	width: 1.5rem;
}

/* SweetAlert2 Custom styles  */
.swal2-styled.swal2-confirm.swalBtnColor {
	background-color: #08bbdf;
}

.product.hover {
	cursor: pointer;
	transition: all 0.3s ease-in;
	-webkit-transform: translate(0, -3%);
	        transform: translate(0, -3%);
	-webkit-animation: shadow-drop-center 0.4s
		cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
	animation: shadow-drop-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.product-layer.hover {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 100%;
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	z-index: 100;
	background: linear-gradient(
		180deg,
		rgba(10, 212, 250, 0.86) 0%,
		rgba(37, 187, 241, 0.86) 100%
	);
}

@-webkit-keyframes shadow-drop-center {
	0% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
	100% {
		box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
	}
}
@keyframes shadow-drop-center {
	0% {
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
	100% {
		box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
	}
}

.product-buyIcon.hover {
	position: absolute;
	top: 5%;
	left: 85%;
	width: 12%;
}

.product-value.hover {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

.product-value.hover span {
	color: #ffffff;
	font-size: 2.25rem;
}

.product-value.hover img {
	padding-top: 0.3rem;
}

.product-redeem_button.hover {
	cursor: pointer;
	text-align: center;
	padding: 0.7rem 4.5rem;
	font-size: 1.1rem;
	border-radius: 6.25rem;
	border: none;
	background-color: #ffffff;
	color: #616161;
	margin-top: 0.5rem;
}

.product-redeem_button.hover:hover {
	background: linear-gradient(90deg, #f5f7fa 0%, #c3cfe2 100%);
}

.product-redeem_button.hover.display-none {
	display: none;
}

.productsList {
	margin-top: 2rem;
	padding: 0 8rem;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 0.8rem;
	gap: 0.8rem;
	justify-items: center;
}

/* Media queries */
@media (max-width: 40rem) {
	/* 640px */
	.productsList {
		padding: 0 2rem;
		grid-template-columns: repeat(1, 1fr);
	}
}

@media (min-width: 40rem) and (max-width: 52.5rem) {
	/* 640px - 720px */
	.productsList {
		padding: 0 6rem;
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (min-width: 52.5rem) and (max-width: 75rem) {
	/* 840px - 1200px */
	.productsList {
		padding: 0 1rem;
		grid-template-columns: repeat(3, 1fr);
	}
}

.hero {
	position: relative;
}

.hero h1 {
	position: absolute;
	top: 65%;
	left: 10%;
	color: #ffffff;
	font-size: 4rem;
}

.hero img {
	max-width: 100%;
}

/* Media queries */
@media (max-width: 28.5rem) {
	/* 456px */
	.hero h1 {
		top: 50%;
		font-size: 2.2rem;
	}
}

@media (min-width: 28.5rem) and (max-width: 37.5rem) {
	/* 456px - 600px */
	.hero h1 {
		top: 50%;
		font-size: 2.8rem;
	}
}

@media (min-width: 37.6rem) and (max-width: 62rem) {
	/* 458px - 992px */
	.hero h1 {
		font-size: 3rem;
	}
}

/* General setting */
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: "Source Sans Pro", sans-serif;
}

.container {
	width: 100%;
	max-width: 96rem; /* 1536px */
}

/* Filters and pagination bars */
.principalBar {
	position: relative;
}

/* Media queries */
@media (min-width: 96rem) {
	/* 1536px */
	.container {
		margin: 0 auto;
	}
}

